//
//  FORMS
//––––––––––––––––––––––––––––––––––––––––––––––––––


//
//  VARIABLES
//––––––––––––––––––––––––––––––––––––––––––––––––––

$form-input-border-radius: 4px;
$form-input-box-shadow: none;
$form-input-font-size: 14px;

//
//  STYLES
//––––––––––––––––––––––––––––––––––––––––––––––––––

.wp-admin {
  input[type="checkbox"],
  input[type="radio"],
  input[type="text"],
  input[type="search"],
  input[type="tel"],
  input[type="time"],
  input[type="url"],
  input[type="week"],
  input[type="password"],
  input[type="color"],
  input[type="date"],
  input[type="datetime"],
  input[type="datetime-local"],
  input[type="email"],
  input[type="month"],
  input[type="number"],
  select,
  textarea {
    box-shadow: $form-input-box-shadow;
    font-size: $form-input-font-size;
  }

  // Text fields.
  input[type="text"],
  input[type="search"],
  input[type="tel"],
  input[type="time"],
  input[type="url"],
  input[type="week"],
  input[type="password"],
  input[type="checkbox"],
  input[type="color"],
  input[type="date"],
  input[type="datetime"],
  input[type="datetime-local"],
  input[type="email"],
  input[type="month"],
  input[type="number"],
  select,
  textarea {
    border: 1px solid $color-gray-d;
    border-radius: $form-input-border-radius;
  }

  select {
    appearance: none;
    background-image:url('data:image/png;base64,');
    background-repeat: no-repeat;
    background-position: 98% 50%;
    line-height: 1.2;
    padding: 3px 5px;
    padding-right: 30px;
  }
}


//
//  FORM TABLE
//––––––––––––––––––––––––––––––––––––––––––––––––––

.wp-admin {
  .form-table {
    input[type="text"],
    input[type="search"],
    input[type="tel"],
    input[type="time"],
    input[type="url"],
    input[type="week"],
    input[type="password"],
    input[type="color"],
    input[type="date"],
    input[type="datetime"],
    input[type="datetime-local"],
    input[type="email"],
    input[type="month"],
    input[type="number"],
    textarea {
      padding: 5px 10px;
    }

    select {
      padding-left: 10px;
      padding-right: 30px;
    }

    input[type="text"],
    input[type="search"],
    input[type="tel"],
    input[type="time"],
    input[type="url"],
    input[type="week"],
    input[type="password"],
    input[type="color"],
    input[type="date"],
    input[type="datetime"],
    input[type="datetime-local"],
    input[type="email"],
    input[type="month"],
    input[type="number"],
    select {
      height: 30px;
    }
  }
}
